le}.hidden{opacity:0;visibility:hidden}.disabled-btn{background-color:grey}.active-btn{background-color:red}.popup{height:100vh;width:100%;position:fixed;top:0;left:0;background-color:rgba(0,0,0,0.8);z-index:500;transition:all 0.2s}.popup-content{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:75%;background-color:white;height:50vh;box-shadow:0 2rem 4rem rgba(0,0,0,0.2);border-radius:2px;font-size:20px}#top-panel{margin-left:100px;position:absolute;height:100px;width:500px;display:flex;justify-content:space-evenly}.status-display{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}#player-energy-num,#player-shields-num,#player-armor-num{margin:5px;font-size:50px;color:#6f6;font-size:40px}#player-shields,#player-armor{margin-top:10px;height:50px;width:100px}#player-energy{border-radius:3px;position:absolute;left:50%;transform:translateX(-50%);top:450px;width:110px;height:80px;color:#6f6}*{font-family:"VT323"}#game-screen{position:relative}#main-outer{display:flex}#main-inner{width:1400px}#cockpit-div{display:flex;width:100%}#cockpit{display:flex;position:absolute;margin:auto}#main-inner{display:block;margin:auto}#game-screen{border:1px solid black;z-index:-1}#img_background,#img_player,#img_npc1,#img_laser,#img_missle,#img_explosion,#img_shields,#img_npc1a1,#img_npc1a2,#img_npc1shields,#img_newshields,#testship{display:none}
